import { useDispatch } from "react-redux";
import { tabAction } from "../store/HorbContextProvider";
import styles from "./Nav.module.css";
import NavItem from "./NavItem";

const Nav = () => {
  const dispatch = useDispatch();
  return (
    <ul className={styles.nav}>
      <NavItem
        onNavClickHandler={() => dispatch(tabAction.viewTab("logs"))}
        variation="normal"
        name="logs"
      />
      <NavItem
        onNavClickHandler={() => dispatch(tabAction.viewTab("form"))}
        variation="pop"
        name="form"
      />
      <NavItem
        onNavClickHandler={() => dispatch(tabAction.viewTab("analytics"))}
        variation="normal"
        name="analytics"
      />
    </ul>
  );
};

export default Nav;
